.discount {
  padding-top: 145px;
  padding-bottom: 170px;
  position: relative;
  background: #fe6257;
  &::after {
    content: "";
    background: white;
    height: 85px;
    width: 100%;
    position: absolute;
    transform: rotate(-3deg);
    bottom: -50px;
    right: 0%;
    .lg(display, none);
  }
  &::before {
    content: "";
    background: white;
    height: 85px;
    width: 100%;
    position: absolute;
    transform: rotate(-3deg);
    top: -50px;
    right: 0%;
    .lg(display, none);
  }
  .title {
    h3 {
      font-size: 36px;
      font-family: 'PT Serif', serif;
      color: rgb(255, 255, 255);
      font-weight: bold;
      span {
        text-transform: uppercase;
      }
    }
    p {
      font-size: 18px;
      color: rgb(255, 255, 255);
      margin-bottom: 35px;
    }
  }
  .discountCard {
    padding-top: 40px;
    padding-bottom: 50px;
    margin-bottom: 30px;
    background-color: rgb(255, 255, 255);
    text-align: center;
    h2 {
      font-size: 24px;
      font-family: 'PT Serif', serif;
      color: rgb(0, 0, 0);
      font-weight: bold;
    }
    p {
      font-size: 18px;
      color: rgb(0, 0, 0);
    }
    .price {
      font-size: 30px;
      color: rgb(254, 98, 87);
      font-weight: bold;
    }
    .btn {
      font-size: 16px;
      color: rgb(255, 255, 255);
      background-color: rgb(101, 207, 183);
      width: 186px;
      height: 42px;
    }
  }
}