footer {
  position: relative;
  background: #212121;
  padding-bottom: 90px;
  padding-top: 15px;
  &::before {
    content: "";
    .lg(display, none);
    background: white;
    height: 85px;
    width: 100%;
    position: absolute;
    transform: rotate(-3deg);
    top: -50px;
    right: 0%;
  }
 
  .title {
    .btngroup {
      position: relative;
      text-align: center;
      margin-bottom: 55px;
      i {
        background: #ffa200;
        color: #212121;
        font-size: 30px;
        width: 45px;
        height: 45px;
        text-align: center;
        line-height: 1.5;
        border-radius: 50%;
      }
    }
    h3{
     
            font-size: 24px;
            font-family: 'PT Serif', serif;
            color: rgb(255, 255, 255);
            text-align: center;
          margin-bottom: 40px;
    }
    img{
        display: block;
        margin: auto;
        margin-bottom: 60px;
    }
  }
  p{
   
        font-size: 14px;
        font-family: "Raleway";
        color: rgb(255, 255, 255);
        margin-bottom: 25px;
        line-height: 1.8;
      
  }
  ul{
      padding: 0;
      li{
          list-style: none;
          a{
       text-decoration: none;
                font-size: 14px;
                color: rgb(255, 255, 255);
                line-height: 1.714;
              
          }
      }
  }
}