header {
  .navbar {
    border-bottom: 1px solid #b4b4b4;
    .navbar-collapse {
      justify-content: end;
      form {
        .lg-block( {
          justify-content: center;
        }
        );
        .number {
          text-decoration: none;
          font-size: 20px;
          color: rgb(0, 0, 0);
          font-weight: bold;
          margin-right: 25px;
          .sm-block( {
            margin: 15px;
          }
          );
        }
        .btn {
          margin-left: 25px;
          border-radius: 20px;
          border: 2px solid rgb(255, 119, 109);
          width: 166px;
          height: 39px;
          background: transparent;
          font-size: 16px;
          color: rgb(255, 119, 109);
          font-weight: bold;
          box-shadow: none;
          .sm-block( {
            margin: 15px;
          }
          );
        }
      }
    }
  }
  .nav {
    .media-block( {
      flex-direction: column;
    }
    , 1025px);
    li {
      a {
        padding: 15px;
        font-size: 14px;
        color: rgb(0, 0, 0);
        text-transform: capitalize !important;
        &.active {
          color: #f2382b;
        }
        &:hover {
          color: #f2382b;
        }
      }
    }
  }
}