.total {
  /*   background: url('../img/totalbg.png') center;
    background-size: cover; */
  background: #9185be;
  padding-bottom: 150px;
  padding-top: 145px;
  position: relative;
  &::after {
    content: "";
    background: white;
    height: 85px;
    width: 100%;
    position: absolute;
    transform: rotate(-3deg);
    bottom: -50px;
    right: 0%;
    .lg(display, none);
  }
  &::before {
    content: "";
    background: white;
    height: 85px;
    width: 100%;
    position: absolute;
    transform: rotate(-3deg);
    top: -50px;
    right: 0%;
    .lg(display, none);
  }
  .title {
    h3 {
      .md-block( {
        font-size: 30px;
      }
      );
      font-size: 36px;
      font-family: 'PT Serif',
      serif;
      color: rgb(255, 255, 255);
      font-weight: bold;
      text-transform: uppercase;
      text-align: center;
      margin-bottom: 40px;
    }
  }
  .post {
    .media-block( {
      flex-basis: 300px !important;
      height: 300px;
    }
    , 400px);
    &:nth-child(1) {
      transform: translateY(20%);
      .xl(transform, translateY(0));
    }
    &:nth-child(2) {
      transform: translateY(0%);
    }
    &:nth-child(3) {
      transform: translateY(-20%);
      .xl(transform, translateY(0));
    }
    border-radius: 50%;
    background: #fff;
    padding: 85px 15px;
    text-align: center;
    height: 320px;
    flex-basis: 320px;
    margin: 30px 15px;
    h2 {
      font-size: 24px;
      font-family: 'PT Serif', serif;
      color: rgb(0, 0, 0);
      font-weight: bold;
    }
    p {
      margin-top: auto;
      font-size: 16px;
      color: rgb(0, 0, 0);
    }
  }
}