.popular {
  padding: 60px 0;
  .title {
    h2 {
      font-size: 36px;
      font-family: "PT Serif";
      color: rgb(65, 62, 62);
      font-weight: bold;
      text-align: center;
      margin-bottom: 50px;
    }
  }
  .card {
    margin: 20px;
    img {
      width: 100%;
    }
    .cardbody {
      text-align: center;
      padding-top: 20px;
      padding-bottom: 30px;
      h2 {
        font-size: 18px;
        color: rgb(65, 62, 62);
        font-weight: bold;
        text-align: center;
        max-width: 272px;
        margin: auto;
        margin-bottom: 25px;
      }
      .cardtext {
        font-size: 16px;
        color: rgb(65, 62, 62);
        max-width: 342px;
        margin-bottom: 30px;
        margin: auto;
      }
      .students {
        font-size: 16px;
        color: rgb(0, 0, 0);
        font-weight: bold;
        background: #f2f2f2;
        height: 40px;
        line-height: 2.5;
      }
      .price {
        font-size: 18px;
        color: rgb(65, 62, 62);
        font-weight: bold;
      }
      .buttonGroup {
        display: flex;
        justify-content: space-around;
        button {
          border-radius: 20px;
          &:nth-child(1) {
            border: 1px solid #b4b4b4;
            color: rgb(65, 62, 62);
            width: 112px;
            font-size: 14px;
          }
          &:nth-child(2) {
            font-size: 16px;
            color: rgb(0, 0, 0);
            background-color: rgb(175, 218, 137);
            width: 114px;
          }
        }
      }
    }
  }
}