.tours {
  background: #afda89;
  padding-top: 200px;
  padding-bottom: 190px;
  position: relative;
  &::after {
    content: "";
    background: white;
    height: 85px;
    width: 100%;
    position: absolute;
    transform: rotate(-3deg);
    bottom: -50px;
    right: 0%;
    .lg(display, none);
  }
  &::before {
    content: "";
    background: white;
    height: 85px;
    width: 100%;
    position: absolute;
    transform: rotate(-3deg);
    top: -50px;
    right: 0%;
    .lg(display, none);
  }
  .toursTitle {
    h2 {
      font-size: 72px;
      font-family: 'PT Serif';
      color: rgb(65, 62, 62);
      font-weight: bold;
      text-transform: uppercase;
      line-height: .5;
      margin: 0;
    }
    h4 {
      font-size: 48px;
      font-family: 'PT Serif';
      color: #fff;
      font-weight: bold;
      margin-bottom: 30px;
    }
    p {
      font-size: 18px;
      color: rgb(0, 0, 0);
      font-weight: 400;
      max-width: 396px;
    }
  }
  .car {
    position: absolute;
    top: 25%;
    .lg(top, 20%);
    .md(top, 55%);
    .sm(top, 65%);
    right: 15%;
    .lg(right, 10%);
    .md(right, 5%);
  }
}