.gids {
  background: #8bccca;
  position: relative;
  padding: 150px 0;
  &::after {
    content: "";
    background: white;
    height: 85px;
    width: 100%;
    position: absolute;
    transform: rotate(-3deg);
    bottom: -50px;
    right: 0%;
    .lg(display, none);
  }
  &::before {
    content: "";
    background: white;
    height: 85px;
    width: 100%;
    position: absolute;
    transform: rotate(-3deg);
    top: -50px;
    right: 0%;
    .lg(display, none);
  }
  .title {
    .md(padding-top, 30px);
    text-align: center;
    h3 {
      font-size: 30px;
      font-family: 'PT Serif', serif;
      color: rgb(255, 255, 255);
      font-weight: bold;
      text-transform: uppercase;
    }
    p {
      font-family: 'Raleway', sans-serif;
      font-size: 20px;
      color: rgb(255, 255, 255);
      max-width: 533px;
      margin: auto;
      margin-bottom: 40px;
    }
  }
  .item {
    text-align: center;
    margin: 30px 0;
    img {
      display: block;
      margin: auto;
      margin-bottom: 20px;
    }
    h2 {
      margin-bottom: 25px;
      font-size: 30px;
      color: rgba(255, 255, 255, 0.502);
      font-weight: bold;
      text-transform: uppercase;
      .lg(font-size, 25px);
      font-family: 'Raleway', sans-serif;
    }
    h3 {
      .lg(font-size, 13px);
      font-size: 30px;
      color: rgb(65, 62, 62);
      font-weight: bold;
      text-transform: uppercase;
      font-family: 'PT Serif', serif;
    }
  }
}