.comments {
 padding: 70px 0;
  position: relative;
  .comment {
      margin-bottom: 35px;
    h2 {
      font-size: 30px;
      font-family: 'PT Serif', serif;
      color: rgb(0, 0, 0);
      font-weight: bold;
    }
    h4 {
      font-size: 16px;
      color: rgb(0, 0, 0);
    }
    .date {
      font-size: 14px;
      color: rgb(0, 0, 0);
    }
    p {
      font-size: 16px;
      color: rgb(0, 0, 0);
      max-width: 483px;
    }
    .btngr {
      display: flex;
      justify-content: space-between;
      button {
        border-radius: 20px;
        border: 1px solid rgb(250, 186, 41);
        width: 114px;
        height: 35px;
        font-size: 14px;
        color: rgb(0, 0, 0);
      }
      a {
        font-size: 14px;
        color: rgb(0, 0, 0);
        text-decoration: underline;
      }
    }
  }
  hr {
    background: #e6e6e6;
    height: 50%;
    width: 2px;
    position: absolute;
    right: 50%;
    top: 28%;
    .lg-block({
      
        height: 1px;
        width: 60%;
        right: 21%;
        top: 50%;
    
  
    });
    .md-block({
      
      width: 70%;
right: 15%;
    
  
    });
    .sm-block({
      
      width: 100%;
right:0%;
    
  
    });
    
  }
}