.register {
  padding-bottom: 35px;
  padding-top: 20px;
  position: relative;
  .container {
    position: relative;
  }
  .pupil {
    position: absolute;
    width: 100%;
    bottom: 20%;
    .lg(bottom, 70%);
    .sm(top, 0%);
  }
  .title {
    text-align: center;
    img {
      display: block;
      margin: auto;
      margin-bottom: 50px;
    }
    h3 {
      margin-bottom: 40px;
      max-width: 700px;
      font-size: 36px;
      font-family: 'PT Serif', serif;
      color: rgb(0, 0, 0);
      font-weight: bold;
      margin: auto;
    }
    p {
      font-size: 24px;
      font-family: 'Raleway', sans-serif;
      color: rgb(64, 64, 64);
      font-weight: bold;
      margin-bottom: 5px;
    }
  }
  form {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    .text {
      width: 100%;
      margin: 20px 5px;
      border-width: 1px;
      border-color: rgb(180, 180, 180);
      border-style: solid;
      height: 33px;
      padding-left: 5px;
      border: 1px solid #bdbdbd;
      background: transparent;
      &:-ms-input-placeholder {
        font-size: 12px;
        color: rgb(65, 62, 62);
      }
    }
    button {
      margin: 20px 5px;
      border-radius: 20px;
      font-size: 16px;
      color: rgb(0, 0, 0);
      background-color: rgb(175, 218, 137);
      width: 114px;
      height: 35px;
    }
  }
}