.ekskurs {
  position: relative;
  .headerBody {
    margin-top: 50px;
    h2 {
      margin-bottom: 25px;
      font-size: 36px;
      color: rgb(0, 0, 0);
      font-weight: bold;
      text-align: center;
      max-width: 775px;
      margin: auto;
      span {
        letter-spacing: -4px;
      }
    }
    p {
      margin: 0;
      font-size: 20px;
      color: rgb(0, 0, 0);
      line-height: 1.2;
      text-align: center;
    }
    img {
      object-fit: cover;
    }
  }
  .ekskursTitle {
    position: absolute;
    width: 100%;
    text-align: center;
    margin: auto;
    top: 70%;
    .md(top, 70%);
    .md(top, 80%);
    h3 {}
    p {}
    button {
      border: none;
      background: transparent;
      display: block;
      margin: auto;
      i {
        display: block;
        line-height: 0.7;
        &:nth-child(1) {
          color: #1f2020;
        }
        &:nth-child(2) {
          color: #5e6161;
        }
        &:nth-child(3) {
          color: #a5aaa9;
        }
      }
    }
    img {
      position: absolute;
      left: 3.3%;
    }
  }
}